.App {
  text-align: center;
  /*background-image: url("./static/confetti.gif");*/
  background-repeat: no-repeat;
  background-size: cover;
  height: 90vh;
  position: absolute;
  font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}




.App-header {
  background-color: #282c34;
  min-height:2vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  width: 100vw;
  position: relative;
}


#body{
  background-color: #282c34;
  min-height:100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#hidden_body{
  display: none;
}

#logged_user_cont{
 position: absolute;
 left: 1vw;
 font-size: 50%
}

#logged_user{
  border-bottom: solid rgba(255,0,0,0.8) 1px;
 }

#login_cont{
  background-color: #282c34;

}

#names_cont{
  background-color: transparent;
  position: absolute;
  left:1vw;
  width:25%;
  height: 100vh;
  color: white;


  
}
#name_list{
  text-align: left;
  overflow: scroll;
  height: 93vh;
  
}

.name_{
  background-color: transparent;
  border-color: transparent;
  color: white;
  width: 15%;
  transition: 2s;
  font-size: 100%;
}

.active_name{
  background-color: red;
  color: white;
  border-color: transparent;
  z-index: -1;
  width: 75%;
  transition: 2s;
  font-size: 80%;

}
.rem_name{
  float: right;
  margin-left: 1vw;
}
.score_{
  float: right;
  font-size: 100%;
  margin-top: 1%;
}

#upper_cont{
  background-color: transparent;
  width: 50%;
  height: 22.7rem;
  margin-left: 25%;
}


#lower_cont{
  display: flex;
  flex-direction: row;
  margin-left: 25%;
}

#word_list{
  width:20%;
  height:45.5vh;
  overflow:scroll;
  font-size: 1rem;
  background-color: transparent;
  color:white;
  text-align: left;
  font-size: 50%;
}

#card_cont{
  width: 46.65%;
  height:45.5vh;
  background-color: transparent;
  color: white;
  z-index: 100;
  overflow: scroll;

}

.card{
  width: 7vw;
  height: 10vh;
  border: #282c34 smooth 1px;
  color: white;
  background-color: rgba(255,255,255,0.2);
  font-size: 20px;
  font-family:monospace;
  padding: 0;
  border-radius: 0%;

  transition: 1s;
  transition-timing-function: ease-in-out;
  transition-duration: 1s;

}

.active_card{
  width: 7vw;
  height: 15vh;
  border: #282c34 solid 1;
  font-size: 20px;
  font-family:monospace;
  background-color: rgba(178, 34, 34,0.9);
  color: white;
  margin: 10%;
  border-radius: 100%;
 

  transition: 1s;
  transition-timing-function: ease-in-out;
  transition-duration: 1s;
}


#img_word{
  margin-top: 2vh;
  height: 25vh;
  background-color: transparent;

}

.syllable_enter{
  width: 15vw;
  height: 15vh;
  background-color: transparent;
  color: white;
  margin-left: 1vw;
  text-align: center;
}

.rem_syl{
  width: 1.5vw;
  height: 1.5vh;
  font-size:1vh;
  text-align: center;
  color: white;
  background-color: rgba(255,0,0,0.5);
  
}


.rem_icon{
  width: 100%;
  height: 100%;
}

.li_cont{
  text-align: left;
  width: 100%;
}
.word_el{
}
.word_len{
  float: right;
  margin-right: 10%
}

#select_cont{
  width: 25vw;
  font-size: 60%;
  margin-left: 37vw;
  color:#282c34;
}

#won_popup{
  background-color: transparent;
  color: #282c34;
  position: realtive;
  width: 50%;
  height:80%;
  margin-left: 25%;
  margin-right: auto;
  
}

#won_message{
  margin-top: 20%;
  font-size: 40px;
}

#active_player_won{
  color: rgba(178, 34, 34,0.9);
  font-size: 50px;
}

#win_bg{
  z-index: 1000;
  margin-left: -50%;
  margin-top: -40%;
  width: 200%;
  position: relative;

}
#win_gif{
  min-width: 18vw;
  position: absolute;
  margin-left: 10vw;
  margin-right: auto;
}

label{
  font-size: 60%;
  margin-left:1vw;
}

input{
  max-width: 40%
}

button{
  font-size: 100%;
}

#reset_word{
  font-size: 50%;
}

#rem_cont{
  margin-top: 1.5vh;
}

#delete_all{
  margin-left:1vw
}

#add_name_btn{
  margin-left: 0.5vw;
}

#singup{
  margin-left: 1vw;
}


::-webkit-scrollbar{
 width: 10px;
 background-color: transparent;
}

::-webkit-scrollbar-track{
  background-color: transparent;
}

::-webkit-scrollbar-thumb{
  background-image: linear-gradient(white,#282c34);
  border-radius: 25px;
  transition: 1s;
}

::-webkit-scrollbar-corner{
  background-color: transparent;
}

